
/* eslint-disable */
import { defineComponent } from 'vue';
import ProjectCard from '@/components/ProjectCard.vue';

function getDir (size:number) {
  if (size % 2 === 1) {
    return "animate-flyInRight";
  }
  return "animate-flyInLeft";
}

interface Project {
  Title: string,
  Description: string, 
  Image: any,
  Dir: string,
  key: number,
}

const projects:Project[] = [];

projects.push({
    Title: "FHE Image Classification Network",
    Description: "In this project, I worked to create a fully encrypted neural network using Fully Homomorphic Encryption(FHE) and Garbled Circuits(GC). For this project, I integrated two C++ libraries and wrote a script that would convert trained networks from Keras into custom files that could be loaded into the encryption. During the project I learned about C++ development, working with Docker containers, and the underlying mathematical algorithms needed to implement machine learning.",
    Image: require('@/assets/HE_GC Diagram for DARPA.png'),
    Dir: getDir(projects.length),
    key: projects.length,
});

projects.push({
    Title: "Fully Homomorphic Encryption Accelerator",
    Description: "I developed a Python library to perform encryption/decryption for two different Fully Homomorphic Encryption schemes, BFV and CKKS. I worked to implement both frameworks to understand the math necessary for encryption and to find ways to decrease the computation time within these encryption schemes. I learned how to effectively read research papers to implement the ideas within code. I also decreased computational work by 500% using the Number Theoretic Transform(NTT) and the Residue Number System(RNS).",
    Image: require('@/assets/Undergraduate Research Symposium.jpg'),
    Dir: getDir(projects.length),
    key: projects.length,
});

projects.push({
    Title: "Predicting Custom Churn",
    Description: "In this project I acted as the lead programmer to create trained networks to predict customer behavior within grocery stores. I learned about cleaning and manipulating large datasets to then run machine learning models on. We created our own custom testing data and tracked customer behavior to determine when a customer would decide to change stores. I learned about the work necessary for creating clean data that allows for machine learning. ",
    Image: require('@/assets/Customer_Churn_Code.png'),
    Dir: getDir(projects.length),
    key: projects.length,
});


projects.push({
    Title: "VPP",
    Description: "One of my first projects during college, I worked on this project with 3 other students for our data structures course. Our goal was to create a simple text editor that would be more intuitive for students to use when working on remote machines. This was our second course where we had to program, and we were working on linux machines using ssh. Students were told to use vim to edit files, which caused many problems for some students as they had issues with the keymappings. So VPP would be a text editor similar to windows programs where you would save with ctrl-s or quit with ctrl-q. In this project I learned about raw vs cooked terminal modes and how to use color control sequences to display custom text colors for different keywords. In this project, I learned about the importance of organizing work amongst the workers. Also, being a manager can be harder than doing all the work by yourself.",
    Image: require('@/assets/VPP.png'),
    Dir: getDir(projects.length),
    key: projects.length,
});

projects.push({
    Title: "Post Quantum Cryptography Security Analysis Tool",
    Description: "For my Software Security Engineering course we had to create a research project and I focused on creating a tool to help ease developers into post quantum cryptography. Post quantum cryptography is a new set of encryption schemes that will be safe against quantum attacks. Some current public key encyprtion schemes, like RSA, are vulnerable to quantum algorithms, specifically Shor's algorithm. While there are not quantum computers that are powerful enough to post a threat to current encryption schemes, we can expect that in the next few years there could be strong enough quantum computers. With this threat, there is a push to standardize new public key encryption schemes that would not be vulnerable to quantum computers. Newer schemes for a post quantum world are being decided by NIST. My project would analyze a developers directory to see if they are using any unsafe functions that would be vulnerable to quantum attacks. I analyzed the version of cryptography library being used and would search through their code for vulnerable function calls. The tool would print out where any potential threats are and allow for the user to update their library. This project was to focus on the potential threat quantum computers pose to current public key encryption schemes and how we would have to change our code to keep it safe in the future. ",
    Image: require('@/assets/Security_Example.png'),
    Dir: getDir(projects.length),
    key: projects.length,
});

