
/* eslint-disable */
import { defineComponent } from 'vue';
// import default_project from '../assets/Default_Project.png';
import { ref } from 'vue';
import { useIntersectionObserver } from '@vueuse/core';

export default defineComponent({
  name: 'ProjectCard',
  props: {
    description: String,
    title: String,
    src: String,
    dir: String,
  },
  data() {
    return {
      picPath: require('@/assets/Default_Project.png')
    }
  },
  mounted() {
    if (this.src) {
      this.picPath = this.src;
    }
  },
  setup() {
    const target = ref(null);
    const targetIsVisible = ref(false);

    const { stop } = useIntersectionObserver(
      target, 
      ([{ isIntersecting }], observerElement:any) => {
        targetIsVisible.value = isIntersecting || targetIsVisible.value; 
      }
    );
    return {
      target, 
      targetIsVisible,
    }
  },
  methods: {
    getImageURL(path:string) {
      return new URL(path, import.meta.url).href;
    },
  }
})

